import React, { useState, useEffect, useRef } from 'react';
import '../styles/menu.scss';
import { toast } from 'react-toastify';
import { PageType } from '../config/PageType';

export default function Menu({ getAllEvents, rules, setPage }) {
   const NavItem = (props) => {
      const [open, setOpen] = useState(false);

      return (
         <li className="menu-button">
            <div
               href="#"
               className="icon-button"
               onClick={() => setOpen(!open)}
            >
               {props.icon}
            </div>

            {open && props.children}
         </li>
      );
   };

   const DropdownMenu = () => {
      const [menuHeight, setMenuHeight] = useState(null);
      const dropdownRef = useRef(null);
      useEffect(() => {
         setMenuHeight(dropdownRef.current?.firstChild.offsetHeight);
      }, []);
      const DropdownItem = (props) => {
         return (
            <div href="#" className="menu-item" onClick={(e) => testOnClick(e)}>
               <span className="icon-button">{props.leftIcon}</span>
               {props.children}
               <span className="icon-right">{props.rightIcon}</span>
            </div>
         );
      };

      return (
         <div
            className="dropdown"
            style={{ height: menuHeight, zIndex: '1' }}
            ref={dropdownRef}
         >
            <div className="menu">
               <DropdownItem
                  goToMenu="main"
                  leftIcon="üìù"
                  onClick={testOnClick}
               >
                  T·∫°o t·∫≠p lu·∫≠t
               </DropdownItem>
               <DropdownItem leftIcon="üóÉÔ∏è" onClick={testOnClick}>
                  Qu·∫£n s·ª± ki·ªán
               </DropdownItem>
               <DropdownItem leftIcon="üó∫" onClick={testOnClick}>
                  T√¨m ƒë·ªãa ƒëi·ªÉm
               </DropdownItem>
               <DropdownItem leftIcon="üì•" onClick={testOnClick}>
                  T·∫£i t·∫≠p lu·∫≠t
               </DropdownItem>
            </div>
         </div>
      );
   };

   const createFile = (fileText, name) => {
      const element = document.createElement('a');
      const file = new Blob(fileText, {
         type: 'text/plain',
      });
      element.href = URL.createObjectURL(file);
      element.download = name;
      document.body.appendChild(element); // Required for this to work in FireFox
      element.click();
   };

   const testOnClick = async (e) => {
      if (e.target.innerText.includes('T·∫£i t·∫≠p lu·∫≠t')) {
         createFile(rules(), 'Rules.txt');
         createFile(await getAllEvents(), 'Events.txt');
      }

      if (e.target.innerText.includes('T√¨m ƒë·ªãa ƒëi·ªÉm')) {
         setPage(PageType.FIND_PLACES);
      }

      if (e.target.innerText.includes('Qu·∫£n s·ª± ki·ªán')) {
         setPage(PageType.MANAGE_EVENTS);
      }

      if (e.target.innerText.includes('T·∫°o t·∫≠p lu·∫≠t')) {
         setPage(PageType.CREATE_RULES);
      }
   };
   return (
      <NavItem icon={<i className="fas fa-bars"></i>}>
         <DropdownMenu></DropdownMenu>
      </NavItem>
   );
}
